/* roboto-regular - latin-ext */

@font-face {
    font-display: swap;
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/roboto-v29-latin-ext-regular.eot");
    /* IE9 Compat Modes */
    src: local(""), url("../fonts/roboto-v29-latin-ext-regular.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("../fonts/roboto-v29-latin-ext-regular.woff2") format("woff2"), /* Super Modern Browsers */
    url("../fonts/roboto-v29-latin-ext-regular.woff") format("woff"), /* Modern Browsers */
    url("../fonts/roboto-v29-latin-ext-regular.ttf") format("truetype"), /* Safari, Android, iOS */
    url("../fonts/roboto-v29-latin-ext-regular.svg#Roboto") format("svg");
    /* Legacy iOS */
}


